import * as THREE from "three";
import Experience from "../Experience.js";

export default class Phase {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.scene = this.experience.scene;
    this.ui = this.experience.debug.tab.pages[1];
    this.orbitControls = this.experience.camera.orbitControls;

    // Parent Object
    this.model = new THREE.Object3D();
    this.scene.add(this.model);

    // Parameter
    this.isShowed = false;
    this.displayedNum = 0;
    this.displayedText = "";

    // UI
    this.ui.addBinding(this, "isShowed", {
      label: "phase",
    });

    this.setModel();
  }

  setModel() {
    this.setStartingLine();
    this.setEndLine();
    this.setAnglePlane();
    this.setAngleNum();
  }

  setStartingLine() {
    this.startingLineEndPoints = [
      new THREE.Vector3(0, 0.85, 0),
      new THREE.Vector3(-0.16, 0.85, 0),
    ];

    this.startingLineGeo = new THREE.BufferGeometry().setFromPoints(
      this.startingLineEndPoints
    );
    this.startingLineMat = new THREE.LineDashedMaterial({
      color: "white",
      dashSize: 0.02,
      gapSize: 0.02,
    });

    this.startingLine = new THREE.LineSegments(
      this.startingLineGeo,
      this.startingLineMat
    );
    this.startingLine.computeLineDistances();

    this.model.add(this.startingLine);
  }

  setEndLine() {
    this.endLineEndPoints = [
      new THREE.Vector3(0, 0.85, 0),
      new THREE.Vector3(-0.3, 0.85, 0),
    ];

    this.endLineGeo = new THREE.BufferGeometry().setFromPoints(
      this.endLineEndPoints
    );
    this.endLineMat = new THREE.LineDashedMaterial({
      color: "white",
      dashSize: 0.02,
      gapSize: 0.02,
    });

    this.endLine = new THREE.LineSegments(this.endLineGeo, this.endLineMat);
    this.endLine.computeLineDistances();

    this.model.add(this.endLine);
  }

  setAnglePlane() {
    this.anglePlaneGeo = new THREE.CircleGeometry(0.12, 32, 0, 2 * Math.PI);
    this.anglePlaneMat = new THREE.MeshBasicMaterial({
      color: "white",
      transparent: true,
      opacity: 0.6,
    });
    this.anglePlaneMat.side = THREE.DoubleSide;
    this.anglePlane = new THREE.Mesh(this.anglePlaneGeo, this.anglePlaneMat);
    this.anglePlane.rotation.set(Math.PI / 2, Math.PI, 0);
    this.anglePlane.position.set(0, 0.85, 0);

    this.model.add(this.anglePlane);
  }

  setAngleNum() {
    this.angleNumCanvas = document.createElement("canvas");
    this.angleNumContext = this.angleNumCanvas.getContext("2d");
    this.angleNumCanvas.width = 512;
    this.angleNumCanvas.height = 256;
    this.angleNumTexture = new THREE.CanvasTexture(this.angleNumCanvas);

    this.angleNumGeo = new THREE.PlaneGeometry(0.1, 0.05, 1, 1);
    this.angleNumMat = new THREE.MeshBasicMaterial({
      map: this.angleNumTexture,
      transparent: true,
      alphaTest: 0.4,
    });
    this.angleNum = new THREE.Mesh(this.angleNumGeo, this.angleNumMat);
    this.angleNum.position.set(0, 0.852, 0);
    this.angleNum.rotation.set(-Math.PI / 2, 0, 0);

    this.model.add(this.angleNum);
  }

  update() {
    // Starting Line
    this.startingLine.rotation.y = this.world.physics.phi;

    // Ending Line
    this.endLine.rotation.y = this.world.physics.angularPos;

    // Angle Plane
    this.anglePlane.geometry.dispose();
    this.anglePlane.geometry = new THREE.CircleGeometry(
      0.12,
      32,
      this.world.physics.phi,
      this.world.physics.angularPosFromPhi
    );

    // Angle Number
    this.angleNumContext.clearRect(
      0,
      0,
      this.angleNumCanvas.width,
      this.angleNumCanvas.height
    );
    this.angleNumContext.fillStyle = "#111111";
    this.angleNumContext.font = "200px Mitr, Arial, sans-serif";
    this.angleNumContext.textAlign = "center";
    this.angleNumContext.textBaseline = "middle";
    if (this.world.physics.unit === "deg") {
      this.displayedNum =
        (this.world.physics.angularPosFromPhi * 180) / Math.PI;
      this.displayedText = this.displayedNum.toFixed(0) + "";
    } else if (this.world.physics.unit === "rad") {
      this.displayedNum = this.world.physics.angularPosFromPhi / Math.PI;
      this.displayedText = this.displayedNum.toFixed(1) + "";
    }
    this.angleNumContext.fillText(
      this.displayedText,
      this.angleNumCanvas.width / 2,
      this.angleNumCanvas.height / 2
    );
    this.angleNumTexture.needsUpdate = true;

    // Angle Number Position & Orientation
    this.angleNum.rotation.z = this.orbitControls.getAzimuthalAngle();
    this.angleNum.position.x =
      -0.05 *
      Math.cos(
        this.world.physics.phi + this.world.physics.angularPosFromPhi / 2
      );
    this.angleNum.position.z =
      0.05 *
      Math.sin(
        this.world.physics.phi + this.world.physics.angularPosFromPhi / 2
      );

    // Visibility
    if (this.isShowed) {
      this.model.visible = true;
    } else {
      this.model.visible = false;
    }
  }
}
