import * as THREE from "three";
import Experience from "../Experience.js";

export default class Environment {
  constructor() {
    this.experience = new Experience();
    this.scene = this.experience.scene;
    this.resources = this.experience.resources;

    this.setEnvironmentMap();
    this.setLight();
  }

  setEnvironmentMap() {
    this.environmentMap = {};
    this.environmentMap.intensity = 1.4;
    this.environmentMap.texture = this.resources.items.environmentMapTexture;
    this.environmentMap.texture.colorSpace = THREE.SRGBColorSpace;

    this.scene.environment = this.environmentMap.texture;

    this.environmentMap.updateMaterials = () => {
      this.scene.traverse((child) => {
        if (
          child instanceof THREE.Mesh &&
          child.material instanceof THREE.MeshStandardMaterial
        ) {
          child.material.envMap = this.environmentMap.texture;
          child.material.envMapIntensity = this.environmentMap.intensity;
          child.material.needsUpdate = true;
        }
      });
    };
    this.environmentMap.updateMaterials();
  }

  setLight() {
    // this.rectLight = new THREE.RectAreaLight("#ffffff", 2, 2, 2);
    // this.rectLight.position.set(-0.8, 0.85, 0);
    // this.rectLight.lookAt(1, 0.85, 0);
    // this.scene.add(this.rectLight);

    this.directionalLight = new THREE.DirectionalLight("#ffffff", 4);
    this.directionalLight.position.set(-0.8, 0.85, 0);
    this.directionalLightTarget = new THREE.Object3D();
    this.directionalLightTarget.position.set(1, 0.85, 0);
    this.scene.add(this.directionalLightTarget);
    this.directionalLight.target = this.directionalLightTarget;
    this.scene.add(this.directionalLight);
  }
}
