import Experience from "../Experience.js";
import { DragControls } from "three/examples/jsm/controls/DragControls.js";

export default class DragHandler {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.canvas = this.experience.canvas;
    this.camera = this.experience.camera;

    this.isDragged = false;

    this.setInstance();
    this.setEvents();
  }

  setInstance() {
    this.instance = new DragControls(
      [this.world.ball.model],
      this.camera.instance,
      this.canvas
    );

    this.instance.rotateSpeed = 0;
    this.instance.recursive = false;
  }

  setEvents() {
    this.instance.addEventListener("dragstart", () => {
      this.camera.orbitControls.enabled = false;
      this.isDragged = true;
      this.instance.addEventListener(
        "dragend",
        () => {
          this.camera.orbitControls.enabled = true;
          this.isDragged = false;
        },
        { once: true }
      );
    });
  }
}
