import * as THREE from "three";
import Experience from "../Experience.js";

export default class Spring {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.scene = this.experience.scene;
    this.ui = this.experience.debug.tab.pages[1];

    // Parameter
    this.isShowed = false;
    this.segments = 6;

    // UI
    this.ui.addBinding(this, "isShowed", {
      label: "spring",
    });

    this.setModel();
  }

  setModel() {
    this.canvas = document.createElement("canvas");
    this.context = this.canvas.getContext("2d");
    this.canvas.width = 512;
    this.canvas.height = 256;
    this.texture = new THREE.CanvasTexture(this.canvas);

    this.silhouetteGeo = new THREE.PlaneGeometry(1, 0.5, 1, 1);
    this.silhouetteMat = new THREE.MeshBasicMaterial({
      map: this.texture,
      transparent: true,
      alphaTest: 0.4,
    });
    this.silhouette = new THREE.Mesh(this.silhouetteGeo, this.silhouetteMat);
    this.silhouette.position.set(1.025 - 0.0012, 0.85, 0);
    this.silhouette.rotation.set(0, -Math.PI / 2, 0);

    this.scene.add(this.silhouette);

    this.wall = new THREE.Mesh(
      new THREE.PlaneGeometry(0.04, 0.16, 1, 1),
      new THREE.MeshBasicMaterial({ color: "#9faabc" })
    );
    this.wall.position.set(-0.496, 0, 0.0001);
    this.silhouette.add(this.wall);
  }

  update() {
    // Canvas
    this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.context.strokeStyle = "#a3aec4";
    // this.context.lineCap = "round";
    this.context.lineJoin = "round";
    this.context.lineWidth = 5;
    this.context.beginPath();
    this.drawPath();
    this.context.stroke();
    this.texture.needsUpdate = true;

    // Visibility
    if (this.isShowed) {
      this.show();
    } else {
      this.hide();
    }
  }

  drawPath() {
    let endPos =
      this.world.shadow.ballShadow.position.z * this.canvas.width +
      this.canvas.width / 2 -
      32;
    let springStart = 25;
    let springEnd = endPos - 20;
    let springLength = springEnd - springStart;
    let segmentLength = springLength / this.segments;
    let yPos = this.canvas.height / 2;

    this.context.moveTo(0, yPos);
    this.context.lineTo(springStart, yPos);
    for (let i = 0; i < this.segments; i++) {
      this.context.lineTo(springStart + (i + 0.25) * segmentLength, yPos + 20);
      this.context.lineTo(springStart + (i + 0.75) * segmentLength, yPos - 20);
      this.context.lineTo(springStart + (i + 1) * segmentLength, yPos);
    }
    this.context.lineTo(endPos, yPos);
  }

  show() {
    this.silhouette.visible = true;
  }

  hide() {
    this.silhouette.visible = false;
  }
}
