import Experience from "../Experience.js";

export default class Physics {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.ui = this.experience.debug.tab.pages[1];
    this.time = this.experience.time;

    // Parameters
    this.angularPos = 0; // measured conter-clockwise from the closest point to the light source, in rad
    this.angularVel = 3.14; // in rad/s
    this.unit = "deg";
    this.phi = 0;
    this.angularPosFromPhi = 0;
    this.isPaused = false;

    // UI
    this.pauseButton = this.ui.addButton({
      label: "control",
      title: "pause/play",
    });
    this.pauseButton.on("click", () => {
      this.isPaused = !this.isPaused;
    });
    this.ui.addBinding(this, "angularVel", {
      label: " (rad/s)",
      min: 0,
      max: 6.28,
    });
    this.ui.addBinding(this, "unit", {
      options: {
        degree: "deg",
        radian: "rad",
      },
    });
    this.ui.addBinding(this, "phi", {
      label: "mode",
      options: {
        "start from x = 0": 0,
        "start from x = +A": Math.PI / 2,
      },
    });
  }

  update() {
    if (this.world.dragHandler.isDragged) {
      this.angularPos = Math.atan2(
        this.world.ball.model.position.z,
        -this.world.ball.model.position.x
      );
    } else if (
      this.world.learning.chap1IsPlaying ||
      this.world.learning.chap2IsPlaying ||
      this.world.learning.chap3IsPlaying
    ) {
      this.angularPos = this.world.learning.angularPos % (2 * Math.PI);
    } else if (this.isPaused == false) {
      this.angularPos =
        (this.angularPos + this.angularVel * this.time.delta) % (2 * Math.PI);
    }

    if (this.angularPos - this.phi < 0) {
      this.angularPosFromPhi = 2 * Math.PI - this.phi + this.angularPos;
    } else {
      this.angularPosFromPhi = this.angularPos - this.phi;
    }
  }
}
