import * as THREE from "three";
import Experience from "../Experience.js";

export default class Shadow {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.scene = this.experience.scene;
    this.resources = this.experience.resources;

    // Texture
    this.ballShadowTexture = this.resources.items.ballShadowTexture;
    this.holderShadowTexture = this.resources.items.holderShadowTexture;

    this.setModel();
  }

  setModel() {
    let ballWorldDiameter = 0.12;
    let ballTextureSize = 512; // in px
    let ballPxDiameter = 360; // in px
    this.ballShadow = new THREE.Mesh(
      new THREE.PlaneGeometry(
        ballWorldDiameter * (ballTextureSize / ballPxDiameter),
        ballWorldDiameter * (ballTextureSize / ballPxDiameter)
      ),
      new THREE.MeshBasicMaterial({
        color: "#a3aec4",
        transparent: true,
        alphaMap: this.ballShadowTexture,
      })
    );
    this.ballShadow.position.set(1.025 - 0.001, 0.85, 0);
    this.ballShadow.rotateY(-Math.PI / 2);
    this.scene.add(this.ballShadow);

    let holderWorldLength = 0.3;
    let holderTextureLength = 1024; // in px
    let holderTextureHeight = 64; // in px
    let holderPxLength = 900; // in px
    this.holderShadow = new THREE.Mesh(
      new THREE.PlaneGeometry(
        holderWorldLength * (holderTextureLength / holderPxLength),
        holderWorldLength * (holderTextureHeight / holderPxLength)
      ),
      new THREE.MeshBasicMaterial({
        color: "#a3aec4",
        transparent: true,
        alphaMap: this.holderShadowTexture,
      })
    );
    this.holderShadow.geometry.translate(holderWorldLength / 2, 0, 0);
    this.holderShadow.position.set(1.025 - 0.001, 0.77, 0);
    this.holderShadow.rotateY(-Math.PI / 2);
    this.scene.add(this.holderShadow);
  }

  update() {
    this.ballShadow.position.z = 0.3 * Math.sin(this.world.physics.angularPos);
    this.holderShadow.scale.x = Math.sin(this.world.physics.angularPos);
  }
}
