import Experience from "../Experience.js";
import * as THREE from "three";

export default class Ball {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.scene = this.experience.scene;

    this.setModel();
  }

  setModel() {
    this.geometry = new THREE.SphereGeometry(0.06, 32, 16);
    this.material = new THREE.MeshStandardMaterial({
      color: "#7CB9E8",
      roughness: 0.08,
      transparent: true,
    });
    this.model = new THREE.Mesh(this.geometry, this.material);

    this.scene.add(this.model);
  }

  update() {
    // Handle Dragging
    if (this.world.dragHandler.isDragged) {
      this.model.translateY(-0.85);
      this.model.position
        .projectOnPlane(new THREE.Vector3(0, 1, 0))
        .normalize()
        .multiplyScalar(0.3);
      this.model.translateY(0.85);

      return;
    }

    this.model.position.set(
      0.3 * -Math.cos(this.world.physics.angularPos),
      0.85,
      0.3 * Math.sin(this.world.physics.angularPos)
    );
  }
}
