import Experience from "../Experience.js";
import Environment from "./Environment.js";
import SceneStatic from "./SceneStatic.js";
import Ball from "./Ball.js";
import Holder from "./Holder.js";
import Physics from "./Physics.js";
import Shadow from "./Shadow.js";
import Phase from "./Phase.js";
import Displacement from "./Displacement.js";
import DragHandler from "./DragHandler.js";
import Velocity from "./Velocity.js";
import Acceleration from "./Acceleration.js";
import Spring from "./Spring.js";
import VelocityAngle from "./VelocityAngle.js";
import AccelerationAngle from "./AccelerationAngle.js";
import Learning from "./Learning.js";

export default class World {
  constructor() {
    this.experience = new Experience();
    this.scene = this.experience.scene;
    this.resources = this.experience.resources;

    // Wait for resources
    this.resources.on("ready", () => {
      // Setup
      this.sceneStatic = new SceneStatic();
      this.ball = new Ball();
      this.physics = new Physics();
      this.holder = new Holder();
      this.shadow = new Shadow();
      this.phase = new Phase();
      this.displacement = new Displacement();
      this.velocity = new Velocity();
      this.velocityAngle = new VelocityAngle();
      this.acceleration = new Acceleration();
      this.accelerationAngle = new AccelerationAngle();
      this.spring = new Spring();
      this.environment = new Environment();
      this.dragHandler = new DragHandler();
      this.learning = new Learning();
    });
  }

  update() {
    if (this.ball) {
      this.physics.update();
      this.ball.update();
    }
    if (this.holder) {
      this.holder.update();
    }
    if (this.shadow) {
      this.shadow.update();
    }
    if (this.phase) {
      this.phase.update();
    }
    if (this.displacement) {
      this.displacement.update();
    }
    if (this.velocity) {
      this.velocity.update();
    }
    if (this.velocityAngle) {
      this.velocityAngle.update();
    }
    if (this.acceleration) {
      this.acceleration.update();
    }
    if (this.accelerationAngle) {
      this.accelerationAngle.update();
    }
    if (this.spring) {
      this.spring.update();
    }
    if (this.learning) {
      this.learning.update();
    }
  }
}
