import * as THREE from "three";
import Experience from "../Experience.js";

export default class Displacement {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.scene = this.experience.scene;
    this.ui = this.experience.debug.tab.pages[1];
    this.orbitControls = this.experience.camera.orbitControls;

    // Parameter
    this.isShowed = false;

    // UI
    this.ui.addBinding(this, "isShowed", {
      label: "pos",
    });

    this.setModel();
  }

  setModel() {
    this.setCircularVector();
    this.setCircularVectorLabel();
    this.setSHMVector();
    this.setSHMVectorLabel();
  }

  setCircularVector() {
    this.circularVector = new THREE.ArrowHelper(
      new THREE.Vector3(-1, 0, 0),
      new THREE.Vector3(0, 0.85, 0),
      0.3,
      "blue",
      0.02,
      0.02
    );

    this.scene.add(this.circularVector);
  }

  setCircularVectorLabel() {
    this.circularVectorCanvas = document.createElement("canvas");
    this.circularVectorContext = this.circularVectorCanvas.getContext("2d");
    this.circularVectorCanvas.width = 128;
    this.circularVectorCanvas.height = 128;
    this.circularVectorTexture = new THREE.CanvasTexture(
      this.circularVectorCanvas
    );

    this.circularVectorLabelGeo = new THREE.PlaneGeometry(0.05, 0.05, 1, 1);
    this.circularVectorLabelMat = new THREE.MeshBasicMaterial({
      map: this.circularVectorTexture,
      transparent: true,
      alphaTest: 0.4,
    });
    this.circularVectorLabel = new THREE.Mesh(
      this.circularVectorLabelGeo,
      this.circularVectorLabelMat
    );
    this.circularVectorLabel.position.set(0, 0.88, 0);

    this.circularVectorContext.fillStyle = "blue";
    this.circularVectorContext.font = "90px Mitr, Arial, sans-serif";
    this.circularVectorContext.textAlign = "center";
    this.circularVectorContext.textBaseline = "middle";
    this.circularVectorContext.fillText(
      "A",
      this.circularVectorCanvas.width / 2,
      this.circularVectorCanvas.height / 2
    );

    this.scene.add(this.circularVectorLabel);
  }

  setSHMVector() {
    this.shmVector = new THREE.ArrowHelper(
      new THREE.Vector3(0, 0, 1),
      new THREE.Vector3(1.022, 0.85, 0),
      0.3,
      "blue",
      0.02,
      0.02
    );

    this.scene.add(this.shmVector);
  }

  setSHMVectorLabel() {
    this.shmVectorCanvas = document.createElement("canvas");
    this.shmVectorContext = this.shmVectorCanvas.getContext("2d");
    this.shmVectorCanvas.width = 512;
    this.shmVectorCanvas.height = 128;
    this.shmVectorTexture = new THREE.CanvasTexture(this.shmVectorCanvas);

    this.shmVectorLabelGeo = new THREE.PlaneGeometry(0.2, 0.05, 1, 1);
    this.shmVectorLabelMat = new THREE.MeshBasicMaterial({
      map: this.shmVectorTexture,
      transparent: true,
      alphaTest: 0.4,
    });
    this.shmVectorLabel = new THREE.Mesh(
      this.shmVectorLabelGeo,
      this.shmVectorLabelMat
    );
    this.shmVectorLabel.rotation.set(0, -Math.PI / 2, 0);

    this.scene.add(this.shmVectorLabel);
  }

  update() {
    // Circular Vector
    this.circularVector.setDirection(
      new THREE.Vector3(
        -1 * Math.cos(this.world.physics.angularPos),
        0,
        1 * Math.sin(this.world.physics.angularPos)
      )
    );

    // Circular Label
    this.circularVectorLabel.rotation.y =
      this.orbitControls.getAzimuthalAngle();
    this.circularVectorLabel.position.set(
      -0.15 * Math.cos(this.world.physics.angularPos),
      0.88,
      0.15 * Math.sin(this.world.physics.angularPos)
    );

    // SHM Vector
    if (Math.sin(this.world.physics.angularPos) < 0) {
      this.shmVector.setDirection(new THREE.Vector3(0, 0, -1));
    } else {
      this.shmVector.setDirection(new THREE.Vector3(0, 0, 1));
    }
    this.shmVector.setLength(
      0.3 * Math.abs(Math.sin(this.world.physics.angularPos)),
      0.02,
      0.02
    );

    // SHM Label
    this.shmVectorContext.clearRect(
      0,
      0,
      this.shmVectorCanvas.width,
      this.shmVectorCanvas.height
    );
    this.shmVectorContext.fillStyle = "blue";
    this.shmVectorContext.font = "90px Mitr, Arial, sans-serif";
    this.shmVectorContext.textAlign = "center";
    this.shmVectorContext.textBaseline = "middle";
    if (Math.abs(this.world.physics.phi) < 0.01) {
      this.shmVectorContext.fillText(
        "Asin" + this.world.phase.displayedText,
        this.shmVectorCanvas.width / 2,
        this.shmVectorCanvas.height / 2
      );
    } else if (Math.abs(this.world.physics.phi - Math.PI / 2) < 0.01) {
      this.shmVectorContext.fillText(
        "Acos" + this.world.phase.displayedText,
        this.shmVectorCanvas.width / 2,
        this.shmVectorCanvas.height / 2
      );
    }
    this.shmVectorTexture.needsUpdate = true;

    // SHM Label Position
    this.shmVectorLabel.position
      .copy(this.shmVector.position)
      .add(
        new THREE.Vector3(
          0,
          0.03,
          (0.3 * Math.sin(this.world.physics.angularPos)) / 2
        )
      );

    // Visibility
    if (this.isShowed) {
      this.show();
    } else {
      this.hide();
    }
  }

  show() {
    this.world.ball.model.material.opacity = 0.6;

    this.circularVector.visible = true;
    this.circularVectorLabel.visible = true;
    this.shmVector.visible = true;
    this.shmVectorLabel.visible = true;
  }

  hide() {
    this.world.ball.model.material.opacity = 1;

    this.circularVector.visible = false;
    this.circularVectorLabel.visible = false;
    this.shmVector.visible = false;
    this.shmVectorLabel.visible = false;
  }
}
