import * as THREE from "three";
import Experience from "../Experience.js";

export default class Holder {
  constructor() {
    this.experience = new Experience();
    this.world = this.experience.world;
    this.scene = this.experience.scene;
    this.resources = this.experience.resources;

    // Resource
    this.modelResource = this.resources.items.sceneModel;

    // Material
    this.material = new THREE.MeshStandardMaterial({
      color: "#dddddd",
      roughness: 0,
      metalness: 1,
    });

    this.setModel();
  }

  setModel() {
    this.modelResource.scene.traverse((child) => {
      if (child.name === "holder") {
        this.model = child;
        this.model.material = this.material;
        this.model.geometry.computeVertexNormals();
      }
    });

    this.scene.add(this.model);
  }

  update() {
    this.model.rotation.y = this.world.physics.angularPos;
  }
}
